.heading {
  font-family: $nunitoSans;
  color: $textColorMain;
  font-style: normal;
  font-weight: 700;

  &_light {
    color: #ffffff;
  }

  &_h1 {
    font-size: 4.2rem;
    margin-bottom: 30px;
    line-height: 100%;

    @include media(1139px) {
      margin-bottom: 20px;
    }

    @include media(767px) {
      font-size: 2.8rem;
      margin-bottom: 10px;
    }
  }

  &_h2 {
    font-size: 4rem;
    margin-bottom: 30px;

    @include media(1139px) {
      font-size: 4rem;
      margin-bottom: 20px;
    }

    @include media(767px) {
      font-size: 2.4rem;
      margin-bottom: 10px;
    }
  }
  &_h3 {
    font-size: 3.2rem;
    line-height: 3.2rem;
    margin-bottom: 10px;

    @include media(767px) {
      font-size: 20px;
      line-height: 2.0rem;
    }
  }

  &_h4 {
    font-size: 1.8rem;
    line-height: 2.4rem;
    margin-bottom: 5px;
    font-weight: 700;

    @include media(767px) {
      font-size: 1.5rem;
      line-height: 2.0rem;
    }
  }
}
