.quality-life {
  overflow: hidden;
  .content {
    padding-top: 40px;
    padding-bottom: 40px;

    @include media(1139) {
      padding-top: 25px;
      padding-bottom: 25px;
    }
    @include media(991) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
    @include media(767px) {
      padding-top: 15px;
      padding-bottom: 15px;
    }

    .heading {
      text-align: center;
      margin-bottom: 5px;
    }
    .subtitle {
      text-align: center;
      color: #535C69;
      margin-bottom: 40px;
      max-width: 410px;
      margin-left: auto;
      margin-right: auto;
      @include media(1139px) {
        margin-bottom: 30px;
      }
      @include media(991px) {
        margin-bottom: 20px;
      }
    }
  }
}