.footer {
  background: #f4f8fb;

  &__navigation {
    display: flex;
    justify-content: space-between;
    padding: 50px 0;

    @include media(991px) {
      padding-bottom: 30px;
    }

    @include media(767px) {
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 30px 0 20px 0;
    }
  }

  &__contact {
    display: flex;
    flex-direction: column;

    @include media(767px) {
      align-items: center;
    }
  }

  & .heading_mb {
    margin-bottom: 15px;
  }

  &__wrap-address {
    display: flex;
    column-gap: 12px;

    & .icon-address {
      color: #26262c;
      width: 12px;
    }
  }

  &__location {
    width: calc(100% - 12px);

    br {
      @include media(767px) {
        display: none;
      }
    }
  }

  &__wrap-tel {
    display: flex;
    column-gap: 10px;
    margin-top: 10px;

    & .icon-tel {
      color: #26262c;
      transition: all 0.2s;
    }
  }

  &__wrap-tel:hover {
    & .icon-tel {
      color: #ff7630;
      transition: all 0.2s;
    }
  }

  &__wrap-social {
    display: flex;
    column-gap: 20px;
    align-items: center;
    margin-top: 16px;

    @include media(767px) {
      margin: 25px 0;
    }
  }

  &__social {
    color: #535c69;
  }

  &__hr {
    height: 1px;
    background: #e2e4e6;
    border: none;
  }

  &__data-policy {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;

    @include media(767px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__policy {
    color: $linkColor;
  }

  &__rights, &__policy {
    @include media(991px) {
      font-size: 1.4rem;
    }

    @include media(575px) {
      font-size: 1rem;
    }
  }
}

.menu-footer {
  display: flex;
  column-gap: 145px;
  max-width: 615px;

  @include media(1139px) {
    column-gap: 100px;
  }

  @include media(991px) {
    flex-direction: column;
    column-gap: 0;

    &__wrap-links + &__wrap-links {
      margin-top: 10px;
    }
  }

  &__wrap-link {
    width: 236px;

    @include media(767px) {
      width: 100%;
    }
  }

  &__wrap-link + &__wrap-link {
    margin-top: 10px;
  }
}
