//  
@import "helpers/_font.scss";

@import "font-circe.scss";
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,800;1,900&display=swap');


// variables font-family
$nunitoSans: 'Nunito Sans', sans-serif;
$irce: 'Circe', sans-serif;

// variables color links and button
$linkColor: #6fc11c;
$linkHover: #79d220;
$linkActive: #70bc23;

// variables color links#2
$linkColor2: #26262c;
$linkHover2: #ff7630;

// variables color button#2
$buttonColor2: #6fc11c;
$buttonHover2: #79D220;
$buttonActive2: #70BC23;

// variables color text
$textColorMain: #26262c;
$textColor2: #535C69;


// 
@mixin media($media) {
  @media only screen and (max-width: $media) {
    @content;
  }
}

// 
html {
  scroll-behavior: smooth;
  font-size: 10px;
}

body {
  font-family: $irce;
  font-size: 1.6rem;
  line-height: 1.3;
  color: $textColorMain;
  font-style: normal;
  font-weight: normal;
  //padding-top: 130px;

  &.bg-default {
    position: relative;
    background-image: url('./public/images/top-background.png');
    background-repeat: no-repeat;
    background-position: top;
    padding-top: 130px;

    @include media(1139px) {
      padding-top: 120px;
    }
    @include media(1139px) {
      padding-top: 105px;
    }
  }
  @include media(767px) {
    font-size: 1.4rem;
  }
}

a {
  text-decoration: none;
  color: $linkColor2;
  font-family: $irce;

  svg {
    path {
      transition: all 0.2s;
    }
  }

  &:hover {
    color: $linkHover2;

    svg {
      path {
        fill: $linkHover2;
      }
    }
  }
}

span {
  font-family: $irce;
}

img {
  max-width: 100%;
}

.container {
  width: 1110px;
  margin: 0 auto;
  padding: 0 10px;

  //  992px  1139px
  @include media(1139px) {
    width: 930px;
    padding: 0 31px;
  }

  //  768px  991px
  @include media(991px) {
    width: 690px;
    padding: 0 39px;
  }

  //  576px  767px
  @include media(767px) {
    width: 510px;
    padding: 0 33px;
  }

  //  320px  575px
  @include media(575px) {
    width: 290px;
    padding: 0 15px;
  }
}

.height100 {
  height: 100%;
  padding: 0;
}

a, a:hover {
  transition: all 0.2s;
}

//  
.hide {
  display: none;
}

.form__tooltip {
  transition: all 0.5s;
}

input, select, textarea {
  box-sizing: border-box;
}

textarea {
  font-family: $irce;
  font-size: 1.6rem;

  &::placeholder {
    color: #A1A7AA;
  }

  &:hover, &:focus {
    &::placeholder {
      color: $textColor2;
    }
  }
}

select, .form__select-leadership {
  border-radius: 10px;
  border: 1px solid #E2E4E6;
  height: 70px;
  padding: 0 30px;
  outline: none;
  font-family: $irce;
  font-size: 1.6rem;
  background-color: #fff;
  display: flex;
  align-items: center;

  &:hover, &:focus, &:active {
    border: 1px solid #A1A7AA;
    outline: 0;

    &::placeholder {
      color: $textColor2;

    }
  }
}

.choices[data-type*='select-one']:after {
  display: none;
}

.choices {
  border: 0;

  &.is-focused, &.is-open {
    .form__select-leadership {
      border-color: #A1A7AA;
      color: $textColor2;
    }
  }

  &.is-open {
    .form__select-leadership {
      border-color: #A1A7AA;
      color: $textColor2;

      &:after {
        transform: rotate(-180deg);
      }
    }
  }
}

.choices__list--dropdown {
  border: 0 !important;
  margin-top: 5px !important;
  box-shadow: 0 0 25px rgb(0 0 0 / 5%);
  border-radius: 10px;
  color: #A1A7AA;

  .is-highlighted {
    background-color: transparent !important;
    color: $textColor2;
  }

  .choices__item {
    padding: 0 0 7px 0 !important;

    &:last-child {
      padding-bottom: 0 !important;
    }
  }

  .choices__list {
    padding: 25px 30px;
  }
}


input, select {
  border-radius: 10px;
  border: 1px solid #E2E4E6;
  outline: none;
  font-family: $irce;
  font-size: 1.6rem;
  //width: 350px;
  width: 100%;
  height: 70px;
  padding: 0 30px;
  background-color: #fff;

  @include media(991px) {
    //width: 290px;
  }

  @include media(767px) {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  @include media(575px) {
    //width: 250px;
    height: 60px;
  }

  &::placeholder {
    color: #A1A7AA;
  }

  &:hover, &:focus, &:active {
    border: 1px solid #A1A7AA;
    outline: 0;

    &::placeholder {
      color: $textColor2;
    }
  }

  &.form__fio, &.form__mail, &.form__vacancy {
    &:focus ~ .form__tooltip,
    &:not(:focus):valid ~ .form__tooltip {
      top: -9px;
      font-size: 1.4rem;
      color: $textColor2;
      background-color: #fff;
      padding: 0 5px;
      transition: font-size 0.3s, top 0.3s;
    }
  }
}

select, .form__select-leadership {
  color: #A1A7AA;
  cursor: pointer;
  appearance: none;
  position: relative;

  @include media(767px) {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  &:after {
    content: '';
    position: absolute;
    background-image: url('./public/images/arrow-select.png');
    background-repeat: no-repeat;
    width: 10px;
    height: 6px;
    right: 30px;
    transition: all 0.2s;
  }


  &:focus, &:hover {
    color: $textColor2;
  }
}

option {
  cursor: pointer;
}

textarea {
  resize: none;
  border-radius: 10px;
  border: 1px solid #E2E4E6;
  outline: none;
  padding: 0;

  &:hover, &:focus {
    border: 1px solid #A1A7AA;
    outline: 0;
    color: $textColor2;
  }
}

.button {
  width: 350px;
  height: 80px;
  border: 0;
  background-color: $buttonColor2;
  color: #fff;
  font-weight: 600;
  border-radius: 10px;
  box-shadow: 0 25px 40px rgba(111, 193, 28, 0.25);
  cursor: pointer;
  font-family: $irce;
  font-size: 1.6rem;
  transition: 0.2s;

  @include media(991px) {
    width: 290px;
    height: 70px;
  }
  @include media(767px) {
    font-size: 1.4rem;
  }
  @include media(575px) {
    width: 250px;
    height: 60px;
  }

  &:active {
    background-color: $buttonActive2;
  }

  &:hover {
    background-color: $buttonHover2;
  }
}
