.development-prospects {
  position: relative;

  @include media(575px) {
    display: flex;
    flex-wrap: wrap;
    column-gap: 50px;
    row-gap: 30px;
  }

  & .heading_h2 {
    text-align: center;
    width: 100%;
    position: absolute;
    right: 0;
    left: 0;

    @include media(575px) {
      position: relative;
    }
  }

  #icons-development-prospects {
    width: 100%;
    height: 100%;

    @include media(575px) {
      display: none;
    }

    circle[stroke-linecap] {
      animation: circleTurn 5s linear;
      animation-iteration-count: 1;
      transform-origin: center center;
      transform-box: fill-box;
    }
  }
}

@keyframes circleTurn {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


.element-prospect {
  display: none;

  @include media(575px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: calc(100% / 2 - 25px);
    row-gap: 5px;
  }

  &__picture {
    border-radius: 50%;
    box-shadow: 0 0 75px rgba(255, 48, 48, 0.4);
  }

  &__desc {
    font-size: 1rem;
  }
}
