.reference-information {
  padding-bottom: 45px;

  @include media(1139px) {
    padding-bottom: 40px;
  }
  @include media(991px) {
    padding-bottom: 30px;
  }
  @include media(575px) {
    padding-bottom: 20px;
  }
  z-index: 10;
  position: relative;

  .reference-content {
    &__items {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: auto;
      background-color: #fff;

      @include media(991px) {
        grid-template-columns: repeat(2, 1fr);
      }

      @include media(575px) {
        grid-template-columns: repeat(1, 1fr);
      }

    }

    &__title {
      font-weight: 700;
      padding-bottom: 10px;
    }

    &__item {
      display: flex;
      flex-direction: column;
      padding: 30px 12px 30px 30px;
      border: 1px solid #F4F8FB;
      border-right: 0;

      @include media(767px) {
        padding: 20px;
      }


      &:last-child {
        border: 1px solid #F4F8FB;
      }

      &.services {
        .services__link {
          display: flex;
          align-items: center;
        }

        svg {
          margin-right: 5px;
        }
      }

      &.social {
        .social__links {
          display: flex;
          align-items: center;

          &__item {
            margin-right: 25px;

            &:last-child {
              margin-right: 0;
            }

            &:hover {
              svg {
                path {
                  fill: $linkColor;
                }
              }
            }
          }
        }
      }

      &.bus {
        .bus__link {
          color: $linkColor;
          display: flex;
          align-items: center;
          white-space: nowrap;

          &:hover {
            color: $linkHover;
          }
        }

        .social__icon {
          margin-left: 10px;
        }
      }
    }
  }
}