.about {
  .content {
    padding-bottom: 40px;
    padding-top: 40px;
    @include media(1139px) {
      padding-bottom: 25px ;
    }
    @include media(991px) {
      padding-bottom: 20px;
    }
    @include media(767px) {
      padding-bottom: 15px;
    }
  }
  &-block {
    display: flex;
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
    @include media(991px) {
      flex-direction: column;
    }
    @include media(767px) {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &__text {
      border: 1px dashed #6FC11C;
      border-radius: 10px;
      padding: 75px 58px;
      max-width: 540px;
      box-sizing: border-box;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-direction: column;
      @include media(1139px) {
        max-width: 450px;
        padding: 44px 30px;
      }
      @include media(991px) {
        max-width: 100%;
        padding: 40px 30px;
        margin-bottom: 20px;
        &:nth-child(2) {
          order: -1;
        }
      }
      @include media(767px) {
        padding: 38px 30px;
      }
      @include media(575px) {
        padding: 34px 20px;
      }
      &:nth-child(2n) {
        margin-left: 30px;
        @include media(991px) {
          margin-left: 0;
        }
      }
    }
    &__img {
      @include media(991px) {
        width: 100%;
      }
      img {
        height: 100%;
        width: 100%;
      }
      &:nth-child(2n) {
        margin-left: 30px;
        @include media(991px) {
          margin-left: 0;
        }
      }
    }
  }
}