.last-news {
  margin-top: 80px;
  margin-bottom: 80px;

  @include media(1139px) {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  @include media(991px) {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  @include media(767px) {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  & .heading_h2 {
    margin-bottom: 35px;
    text-align: center;

    @include media(1139px) {
      margin-bottom: 20px;
    }

    @include media(767px) {
      margin-bottom: 10px;
    }
  }
}
