.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  min-height: 80px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin-top: 20px;
  padding: 0 40px;
  box-sizing: border-box;
  background: #ffffff;
  z-index: 50;

  @include media(1139px) {
    min-height: 70px;
    padding: 0 30px;
  }

  @include media(575px) {
    padding: 0 20px;
  }

  //&.js-active {
  //  z-index: 100;
  //}

  // 
  &__logo {
    display: block;
    width: 160px;

    @include media(1139px) {
      width: 130px;
    }
  }

  // 
  .menu {
    $self: &;

    display: flex;
    column-gap: 35px;

    @include media(1139px) {
      column-gap: 25px;
    }

    @include media(991px) {
      display: none;
      &_js-active {
        display: flex;
      }
    }

    &__link {
      color: $linkColor2;
      text-decoration: none;
      display: flex;
      align-items: center;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        color: $linkHover2;
      }

      &_mobile {
        display: none;

        @include media(991px) {
          display: block;
        }
      }

      &_desktop {
        @include media(991px) {
          display: none;
        }
      }

      &_drop-list {
        position: relative;
        cursor: pointer;

        @include media(991px) {
          display: none;
        }

        .icon-drop-arrow {
          margin-left: 4px;
          transition: all 0.2s;
        }

        &:hover {
          .icon-drop-arrow {
            transform: rotate(180deg);
            transition: all 0.2s;
          }

          .menu__wrap-drop-list {
            opacity: 1;
            transition: all 0.2s;
            padding: 40px;
            height: auto;
            z-index: 0;

            .menu__link {
              height: auto;
            }
          }
        }
      }
    }

    //   
    &__wrap-drop-list {
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      background: #ffffff;
      top: 20px;
      right: 0;
      width: max-content;
      border-radius: 0 0 10px 10px;
      box-shadow: -40px 40px 25px -40px rgba(0, 0, 0, 0.05), 40px 40px 25px -40px rgba(0, 0, 0, 0.05);
      cursor: default;
      opacity: 0;
      padding: 0;
      height: 0;
      z-index: -1;

      .menu__link {
        margin-bottom: 7px;
        height: 0;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &_js-active {
      display: flex;
      flex-direction: column;
      //align-items: center;
      //position: absolute;
      //width: 100%;
      //height: 100%;
      //z-index: 150;
      //margin-top: 60px;
      //left: 0;
      //right: 0;
      //overflow-y: scroll;

      .menu__link {
        color: #ffffff;

        & + .menu__link {
          margin-top: 12px;
        }
      }
    }
  }
}

.bg-mobile-menu {
  width: 0;
  transition: all 0.2s;

  .menu {
    @include media(991px) {
      display: none;
    }
    &_js-active {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 60px;
      a {
        color: #fff;
        padding-bottom: 12px;
        &:last-child {
          padding-bottom: 0;
        }
      }
      .btn-close {
        display: block;
        width: 20px;
        height: 20px;
        background-color: transparent;
        position: absolute;
        right: 40px;
        top: 60px;
      }
      .menu__wrap-drop-list {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }

  &_js-active {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    z-index: 150;
    transition: all 0.2s;
    background: rgba(28, 28, 41, 0.98);
  }
}

.btn-close {
  display: none;

  &_js-active {
  //  position: absolute;
  //  display: block;
  //  right: 0;
  //  border: none;
  //  padding: 0;
  //  width: 25px;
  //  height: 25px;
  //  background: transparent;
  }


  &:before, &:after {
    content: '';
    display: block;
    width: 26px;
    height: 3px;
    background: #ffffff;
    position: absolute;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.socials {
  display: none;

  &_js-active {
    display: block;
  }
}
