.news-item {
  padding: 30px 40px;
  border-radius: 10px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  height: 362px;
  box-sizing: border-box;

  @include media(1139px) {
    height: 302px;
    padding: 20px;
  }
  @include media(991px) {
    height: 222px;
    padding: 15px 20px;
  }
  @include media(767px) {
    height: 162px;
    padding: 15px;
  }
  @include media(575px) {
    height: 180px;
    padding: 20px;
  }

  .heading {
    color: #fff;
  }

  &__description {
    color: #fff;
    margin-bottom: 5px;

    @include media(1139px) {
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      height: 43px;
    }
    @include media(767px) {
      -webkit-line-clamp: 3;
      height: 53px;
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
    @include media(575px) {
      -webkit-line-clamp: 2;
      height: 38px;
    }
  }

  &__link {
    color: $linkColor;
    font-weight: 600;
    line-height: 2rem;
    @include media(767px) {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }

    svg {
      margin-left: 10px;
    }

    &:hover {
      color: $linkHover;
      svg {
        path {
          fill: $linkHover;
        }
      }
    }
  }
}