.asked-questions {

  & .heading {
    text-align: center;
    margin-bottom: 45px;

    @include media(1139px) {
      margin-bottom: 35px;
    }

    @include media(991px) {
      margin-bottom: 20px;
    }
  }

  &__wrap-questions {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 30px;

    @include media(991px) {
      row-gap: 25px;
    }
  }
}
