.grid-block-leadership {
  padding: 30px 35px 40px 35px;
  border-top: 10px solid #6FC11C;
  border-radius: 10px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.05);

  @include media(1139px) {
    padding: 20px 20px 40px 20px;
  }

  @include media(991px) {
    padding: 20px 30px;
  }

  @include media(767px) {
    padding: 30px 30px 40px 35px;
  }

  @include media(575px) {
    padding: 20px 40px 30px 40px;
  }

  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include media(767px) {
      flex-direction: row;
    }

    @include media(575px) {
      flex-direction: column;
    }
  }

  &__avatar {
    width: 180px;
    height: 180px;
    border-radius: 100%;
    background-color: #E1E8EC;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    margin-bottom: 20px;

    @include media(1139px) {
      width: 150px;
      height: 150px;
      flex-shrink: 0;
    }

    @include media(767px) {
      margin-right: 85px;
      margin-bottom: 0;
    }

    @include media(575px) {
      margin: 0 auto 10px auto;
    }
  }

  &__title {
    font-weight: 700;
    margin-bottom: 5px;

    @include media(575px) {
      white-space: nowrap;
    }
  }
  &__subtitle {
    font-size: 1.4rem;
    line-height: 1.8rem;
    color: $textColor2;
    @include media(767px) {
      font-size: 1rem;
      line-height: 1.3rem;
    }

  }
  &__title, &__subtitle, &__phone, &__email {
    text-align: center;

    @include media(767px) {
      text-align: left;
    }
    @include media(575px) {
      text-align: center;
    }
  }
  &__item {
    &_phone, &_email {
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        margin-right: 5px;
      }
      @include media(767px) {
        justify-content: flex-start;
      }
      @include media(575px) {
        justify-content: center;
      }
    }
  }
  &__phone {
    margin-top: 40px;
    margin-bottom: 5px;
    @include media(767px) {
      margin-top: 35px ;
    }
  }
}
