.gallery {
  .content {
    padding-top: 40px;
    padding-bottom: 40px;

    @include media(1139) {
      padding-top: 25px;
      padding-bottom: 25px;
    }
    @include media(991) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
    @include media(767px) {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }

  .heading {
    text-align: center;
    margin-bottom: 20px;

    @include media (767px) {
      margin-bottom: 10px;
    }
  }

  .gallery-items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    overflow: hidden;

    @include media(575px) {
      grid-template-columns: repeat(2, 1fr);
    }

    &__img {
      height: 100%;
      width: 100%;
      transition: all 0.3s;

      &:hover {
        transform: scale(1.01);
        transition: all 0.3s;
        cursor: pointer;
      }
    }
  }
}
