.leadership-peoples {
  .content {
    padding-bottom: 80px;

    @include media(1139px) {
      padding-bottom: 50px;
    }
    @include media(991px) {
      padding-bottom: 40px;
    }
    @include media(767px) {
      padding-bottom: 20px;
    }
    @include media(575px) {
      padding-bottom: 30px;
    }

  }

  .heading_h2 {
    text-align: center;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    grid-gap: 30px;

    @include media(991px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include media(767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}