.tabs-content {

  @include media(767px) {
    z-index: 50;
    position: relative;
  }

  &__item {
    display: none;

    @include media(991px) {
      flex-direction: column;
    }

    &.active-content {
      display: flex;
      animation: fadeOpacity 1.5s;
    }

    @keyframes fadeOpacity {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }
  }

  &__img {
    width: 540px;
    height: 400px;
    object-fit: cover;
    border-radius: 10px;

    @include media(1139px) {
      width: 450px;
      height: 340px;
    }

    @include media(991px) {
      width: 100%;
      margin-bottom: 20px;
    }

    @include media(767px) {
      margin-bottom: 10px;
    }

    @include media(575px) {
      height: 170px;
    }
  }

  .info {
    margin-left: 30px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;

    @include media(991px) {
      margin-left: 0;
    }

    &__title {
      font-size: 18px;
      line-height: 24.5px;
      font-weight: 600;
      margin-bottom: 5px;

      @include media(767px) {
        font-size: 15px;
        line-height: 20.4px;
        margin-bottom: 10px;
      }
    }

    &__description {
      font-size: 16px;
      line-height: 20.8px;
      color: #535C69;
      margin-bottom: 30px;
      max-height: 235px;
      overflow-y: auto;

      @include media(1139px) {
        max-height: 180px;
      }

      @include media(991px) {
        margin-bottom: 20px;
        max-height: unset;
      }

      @include media(767px) {
        font-size: 14px;
        line-height: 18.2px;
      }

      @include media(575px) {
        margin-bottom: 10px;
      }
    }

    &__advantages {
      padding-top: 30px;
      border-top: 1px solid #E2E4E6;
      width: 100%;

      @include media(991px) {
        padding-top: 20px;
      }

      @include media(575px) {
        padding-top: 10px;
      }
    }

    &__item {
      font-size: 16px;
      line-height: 20.8px;
      font-weight: 600;
      margin-bottom: 5px;
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 14px;

      @include media(767px) {
        font-size: 14px;
        line-height: 18.2px;
      }

      &:before {
        content: '';
        position: absolute;
        width: 4px;
        height: 4px;
        background-color: #FF7630;
        border-radius: 50%;
        left: 0;
        top: 7px;
        box-shadow: 0 0 20px rgba(255, 118, 48, 0.5);
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
