.tabs-wrapper {
  @include media(767px) {
    width: 510px;
    overflow-x: auto;
    overflow-y: hidden;
    margin-bottom: -10px;
  }
  @include media(575px) {
    width: 290px;
  }
}
.tabs {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 15px;
  border-bottom: 1px solid #E2E4E6;
  border-radius: 2px;
  margin: 0 95px 30px;

  @include media(1139px) {
    margin-left: 0;
    margin-right: 0;
  }
  @include media(991px) {
    margin-bottom: 20px;
  }
  @include media(767px) {
    margin-bottom: 25px;
    justify-content: flex-start;
    padding-bottom: 15px;
    margin-left: 0;
    margin-right: 0;
    position: relative;
    border-bottom: 0;
    &:before {
      content: '';
      width: 135%;
      height: 1px;
      background-color: #E2E4E6;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      @include media(575px) {
        width: 238%;
      }
    }
  }
  @include media(575px) {
    padding-bottom: 10px;
  }

  &__item {
    color: #A1A7AA;
    font-size: 12px;
    line-height: 12px;
    text-transform: uppercase;
    padding: 0 20px;
    cursor: pointer;
    font-weight: 600;
    position: relative;
    @include media(1139px) {
      padding: 0 23px;
    }
    @include media(991px) {
      padding: 0 8px;
    }
    @include media(767px) {
      font-size: 10px;
      line-height: 10px;
      padding: 0 15px;
      white-space: nowrap;
    }

    &.active-tab {
      color: #26262C;
      &:after {
        content: '';
        position: absolute;
        height: 3px;
        width: 100%;
        background-color: #6FC11C;
        bottom: -16px;
        left: 0;
        border-radius: 2px 2px 0 0;
        @include media(575px) {
          bottom: -10px;
        }
      }
    }
  }
}