.modal-window {
  width: 100%;
  position: fixed;
  top: 50%;
  z-index: 99;
  height: 0;
  overflow-x: auto;
  opacity: 0;
  background: rgba(31, 42, 56, 0.95);

  &__content {
    width: fit-content;
    margin: auto;
    transition: all 0.7s;
    transform: scale(0,0);
    position: relative;
    height: 85vh;
  }

  &__btn-close {
    height: 19.5px;
    width: 19.5px;
    position: absolute;
    cursor: pointer;
    right: 20px;
    top: -30px;
    border: none;
    background: none;

    @include media(479px) {
      height: 17px;
      width: 17px;
      right: 15px;
      top: 15px;
    }

    &::before, &::after {
      content: '';
      display: block;
      height: 1px;
      width: 100%;
      background: #ffffff;
      position: absolute;
      top: 50%;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}

.js-open-modal {
  height: 100%;
  top: 0;
  opacity: 1;
  display: flex;
  justify-content: center;

  & .modal-window__content {
    transition: all 0.7s;
    transform: scale(1,1);
  }
}

.js-overflow-hidden {
  overflow: hidden;

  & main > div, footer {
    padding-right: 15px;
  }
}
