.resource-organization {
  padding-bottom: 50px;

  @include media(991px) {
    padding-bottom: 40px;
  }
  @include media(767px) {
    padding-bottom: 30px;
  }
  @include media(575px) {
    padding-bottom: 20px;
  }

  .resource-content {
    &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: auto;

      @include media(1139px) {
        grid-template-rows: 198px 1fr;
      }

      @include media(991px) {
        grid-template-rows: 188px 1fr;
        grid-template-columns: repeat(2, 1fr);
      }
      @include media(767px) {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: 150px 1fr;
      }
      @include media(575px) {
        grid-template-rows: 128px 1fr;
      }
    }

    .grid-block-organization {
      display: flex;
      flex-direction: column;
      border-right: 0;

      @include media(767px) {
        padding: 20px;
      }

      &:last-child {
        border-right: 1px solid #F4F8FB;
      }

      &__header {
        flex-grow: 1;
        margin-bottom: 20px;

        @include media(575px) {
          margin-bottom: 10px;
        }
      }

      &__title {
        margin-bottom: 2px;
      }

      &__description {
        color: #535C69;
      }
    }
  }

}