@include full-screen-bg('asked-questions') {
  background: url("./public/images/bg-asked-questions.png") no-repeat;
  background-size: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
  margin-top: 115px;

  @include media(1139px) {
    background: url("./public/images/bg-asked-questions-992.png") no-repeat center;
    padding-top: 50px;
    background-size: cover;
    margin-top: 50px;
  }

  @include media(991px) {
    margin-top: 40px;
  }

  @include media(767px) {
    padding-top: 30px;
    margin-top: 30px;
  }
}
