.actual-vacancy {
  .content {
    padding-top: 40px;
    padding-bottom: 40px;

    @include media(991px) {
      padding-top: 260px;
      padding-bottom: 50px;
    }

    @include media(767px) {
      padding-top: 320px;
      padding-bottom: 40px;
    }

    @include media(575px) {
      padding-top: 310px;
    }
  }

  &__info {
    background-image: url("./public/images/actual-vacacy-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    padding: 40px 0;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    @include media(575px) {
      padding: 20px 15px;
    }
    &__subtitle, .heading {
      color: #fff;
    }

    .heading {

      line-height: 5rem;
      margin-bottom: 5px;

      @include media(767px) {
        line-height: 3rem;
      }
    }
    &__subtitle {
      font-size: 1.8rem;
      line-height: 2.4rem;
      margin-bottom: 35px;
      @include media(767px) {
        font-size: 1.5rem;
        line-height: 2.0rem;
        margin-bottom: 20px;
        max-width: 380px;
        br {
          display: none;
        }
      }
      @include media(575px) {
        max-width: 250px;
        margin-bottom: 15px;
        br {
          display: block;
        }
      }
    }
    .button {
      display: flex;
      align-items: center;
      justify-content: center;
      &:hover {
        color: #fff;
      }
    }
  }
}
