.usefull-phones {
  padding-bottom: 50px;
  @include media(991px) {
    padding-bottom: 40px;
  }
  @include media(767px) {
    padding-bottom: 30px;
  }
  .phones-content {
    &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: auto;
      border-right: 1px solid #F4F8FB;
      border-bottom: 1px solid #F4F8FB;

      @include media(1139px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @include media(767px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    .grid-block-organization {

      @include media(991px) {
        padding-right: 16px;
      }
      @include media(767px) {
        padding: 20px 20px 20px 20px;
      }
      &:nth-child(1n) {
        border-right: 0;
        border-bottom: 0;
      }
      &__header {
        margin-bottom: 10px;
      }
      &__footer {
        display: flex;
      }
      &__link {
        display: flex;
        align-items: center;
        margin-right: 15px;
        @include media(991px) {
          white-space: nowrap;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}