.personal-form {
  background-image: url('./public/images/personal-centr.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 1114px;
  position: relative;

  @include media(1139px) {
    height: 720px;
  }

  @include media(767px) {
    height: 600px;
  }

  .decor {
    position: absolute;
    right: 100px;
    bottom: -20px;
    z-index: 0;
    max-width: 1167px;
    width: 100%;

    @include media(1139px) {
      max-width: 715px;
      right: 60px;
      bottom: 0;
    }

    @include media(991px) {
      max-width: 603px;
    }

    @include media(767px) {
      max-width: 507px;
      right: 0;
    }

    @include media(575px) {
      display: none;
    }
  }

  .container {
    height: 100%;
  }

  .content {
    display: flex;
    align-items: center;
    height: 100%;

    @include media(991px) {
      flex-direction: column;
      padding-top: 220px;
      align-items: flex-start;
    }

    @include media(767px) {
      padding-top: 150px;
    }
  }

  &__text {
    max-width: 540px;
    width: 100%;
    color: #fff;
    margin-right: 30px;
    z-index: 5;

    @include media(991px) {
      margin-right: 0;
    }

    .heading {
      color: #fff;

      margin-bottom: 10px;
      @include media(767px) {
        max-width: 450px;
      }
    }

    .description {
      font-size: 2rem;
      line-height: 2.7rem;
      font-family: $nunitoSans;

      @include media(991px) {
        padding-bottom: 105px;
      }

      @include media(767px) {
        font-size: 1.5rem;
        line-height: 2rem;
        padding-bottom: 60px;
      }

      @include media(575px) {
        padding-bottom: 45px;
      }
    }
  }

  &__form {
    @include media(1139px) {
      position: relative;
      top: 50px;
    }

    @include media(991px) {
      top: 0;
    }

    .main-form {
      @include media(1139px) {
        border-radius: 0%/10%;
      }

      .form {
        flex-direction: column;
        padding: 60px 95px;

        @include media(1139px) {
          padding: 50px
        }

        @include media(991px) {
          padding: 50px 40px 40px 40px;
        }

        @include media(767px) {
          padding: 40px 110px;
        }

        @include media(575px) {
          padding: 30px 20px;
        }

        .heading {

          @include media(991px) {
            text-align: center;
          }

          @include media(767px) {
            margin-bottom: 20px;
          }

          @include media(575px) {
            margin-bottom: 40px;
          }
        }

        &__control-items-col1 {

          column-gap: 30px;
          margin-bottom: 30px;

          @media only screen and (max-device-width: 991px) and (min-device-width: 768px) {
            width: 100%;
            flex-direction: row;
            flex-wrap: wrap;
            margin-bottom: 0;
          }
        }

        &__control-item {

          @media only screen and (max-device-width: 991px) and (min-device-width: 768px) {
            width: calc(100% / 2 - 15px);
          }
        }

        &__persona-info {
          br {
            display: none;
          }
        }
      }
    }
  }
}
