@include full-screen-bg('subscribe-news') {
  background: #535c69;
  padding: 30px 0 35px 0;
  margin-top: 80px;

  @include media(1139px) {
    margin-top: 50px;
    padding-bottom: 30px;
  }

  @include media(991px) {
    margin-top: 40px;
  }

  @include media(767px) {
    margin-top: 30px;
    padding: 20px 0;
  }
}

.subscribe-news {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  & .heading_mb {
    margin-bottom: 10px;

    @include media(1139px) {
      margin-bottom: 5px;
    }
  }

  &__description{
    color: #ffffff;
  }

  &__wrap-social {
    display: flex;
    align-items: center;
    column-gap: 20px;
    margin-top: 15px;

    @include media(767px) {
      margin-top: 10px;
    }
  }

  &__social {
    color: #ffffff;
  }
}
