.question {
  display: flex;
  flex-direction: column;
  row-gap: 7px;
  justify-content: center;
  position: relative;
  width: 350px;
  min-height: 150px;
  box-sizing: border-box;
  background: #ffffff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.2);

  @include media(1139px) {
    min-height: 135px;
    width: 450px;
  }

  @include media(991px) {
    width: 330px;
  }

  @include media(767px) {
    width: 100%;
  }

  &__icon {
    position: absolute;
    left: 0;
    top: -16px;
    width: 32px;
    height: 32px;
  }

  &__title {
    font-family: $nunitoSans;
    font-style: normal;
    font-weight: bold;
    font-size: 1.8rem;
    line-height: 1.38;
    color: $textColorMain;

    @include media(767px) {
      font-size: 1.5rem;
    }
  }

  &__description {
    color: $textColor2;

    @include media(767px) {
      font-size: 1.4rem;
    }
  }
}
