.news {
  .content {
    padding-bottom: 50px;
    @include media(991px) {
      padding-bottom: 40px;
    }
    @include media(767px) {
      padding-bottom: 30px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    @include media(575px) {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 20px;
    }
  }

  &__button {
    margin-top: 30px;
    width: 100%;
    background: #F4F8FB;
    border-radius: 5px;
    color: #535C69;
    box-shadow: none;
    display: flex;
    align-items: center;
    justify-content: center;

    @include media(991px) {
      margin-top: 20px;
    }

    @include media(767px) {
      font-size: 1.4rem;
    }

    svg {
      margin-right: 12px;

      path {
        transition: 0.2s;
      }
    }

    &:hover {
      background: #E2E4E5;
    }

    &:active {
      background: #A1A7AA;
      color: #fff;

      svg {

        path {
          fill: #fff;
        }
      }
    }
  }
}
