.swiper {
  width: 100%;
  height: 100%;
  border-radius: 10px;

  &_your-quarter {
    height: 600px;

    @include media(1139px) {
      height: 500px;
    }

    @include media(991px) {
      height: 370px;
    }

    @include media(767px) {
      height: 275px;
    }

    @include media(575px) {
      height: 100%;

      .heading_h3 {
        color: #26262c;
      }
    }
  }

  & .swiper-slide {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: end;

    @include media(575px) {
      flex-direction: column;
    }

    &__image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: 0;

      @include media(575px) {
        position: relative;
        height: 155px;
        border-radius: 10px;
      }
    }

    &__filter {
      background: linear-gradient(360deg, #131634 0%, rgba(19, 22, 52, 0) 47.27%);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      @include media(575px) {
        background: none;
      }
    }

    &__wrap-data {
      z-index: 1;
      padding-bottom: 45px;
      max-width: 790px;

      @include media(1139px) {
        max-width: 770px;
      }

      @include media(991px) {
        max-width: 570px;
      }

      @include media(767px) {
        max-width: 470px;
      }

      @include media(575px) {
        margin-top: 10px;
      }
    }

    &__desc {
      color: #ffffff;

      @include media(575px) {
        color: #535c69;
      }
    }
  }

  & .swiper-button-prev {
    left: 0;
  }

  & .swiper-button-next {
    right: 0;
  }

  & .swiper-button-prev, & .swiper-button-next {
    height: 100%;
    width: 37px;
    top: 0;
    margin: 0;
    background: rgb(38, 38, 44, 0.1);

    @include media(575px) {
      display: none;
    }

    &:after {
      color: #ffffff;
      font-size: 13px;
    }

    &:hover {
      background: rgb(38, 38, 44, 0.35);
    }
  }

  & .swiper-pagination {
    display: flex;
    justify-content: center;
    align-items: center;

    & .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
      background: #ffffff;
      opacity: 0.25;

      @include media(575px) {
        width: 4px;
        height: 4px;
        background: #26262c;
      }

      &-active {
        background: #6fc11c;
        opacity: 1;
        border: 3px solid rgba(255, 255, 255, 0);
        outline: 1px solid #6fc11c;
        background-clip: padding-box;

        @include media(575px) {
          border: 2px solid rgba(255, 255, 255, 0);
        }
      }
    }
  }
}
