.location {
  position: relative;

  .content {
    padding-top: 40px;
    padding-bottom: 40px;

    @include media(1139) {
      padding-top: 25px;
      padding-bottom: 25px;
    }
    @include media(991) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
    @include media(767px) {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }

  .heading {
    margin-bottom: 0;
    text-align: center;
  }

  .heading, .subtitle, .description {
    text-align: center;
  }

  .subtitle, .description {
    color: #535C69;
  }

  .subtitle {
    margin-bottom: 5px;
    font-size: 18px;
    line-height: 24.5px;
    font-weight: 600;
  }

  .description {
    max-width: 790px;
    margin: 0 auto 40px;
  }

  & .location-items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 145px;

    @include media(1139px) {
      grid-column-gap: 85px;
    }

    @include media(991px) {
      grid-column-gap: 60px;
      grid-template-columns: repeat(3, 1fr);
      grid-row-gap: 15px;
    }

    @include media(767px) {
      grid-column-gap: 125px;
      grid-template-columns: repeat(2, 1fr);
    }

    @include media(575px) {
      grid-column-gap: 50px;
      grid-row-gap: 10px;
    }

    &__item {
      display: flex;
      align-items: center;
      cursor: pointer;
      position: relative;
    }

    &__title {
      margin-left: 10px;
      white-space: nowrap;

      @include media(575px) {
        font-size: 1rem;
        white-space: break-spaces;
      }
    }

    &__tooltip {
      position: absolute;
      top: 95px;
      background-color: #fff;
      border-radius: 5px;
      box-shadow: 0 0 10px #C4C4C4;
      padding: 20px;
      width: 285px;
      font-size: 14px;
      line-height: 18.2px;
      color: #535C69;
      z-index: -1;
      opacity: 0;
      transition: 0.2s;
      transform: translateY(20px);
    }

    &:hover  .location-items__tooltip {
      opacity: 1;
      z-index: 50;
      transform: translateY(0px);
    }
  }
}

*[data-tooltip]:hover:after {
  content: attr(data-tooltip);
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
  border-radius: 5px;
  padding: 20px;
  position: absolute;
  top: 45px;
  z-index: 1;
  color: #535c69;
  font-size: 1.4rem;
  width: 100%;

  @include media(767px) {
    font-size: 1rem;
    width: auto;
  }
}

.map {
  width: 100%;
  height: 800px;

  @include media(1139) {
    height: 600px;
  }

  @include media(991px) {
    height: 460px;
  }

  @include media(767px) {
    height: 400px;
  }

  @include media(575px) {
    height: 220px;
  }
}

.map-rounding {
  position: absolute;
  bottom: 0;
}
