.personal-center-card {
  .content {
    padding-bottom: 80px;

    @include media(1139px) {
      padding-bottom: 50px;
    }
    @include media(991px) {
      padding-bottom: 40px ;
    }
    @include media(767px) {
      padding-bottom: 30px ;
    }
  }
  .header-title {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 30px;
    @include media(991px) {
      padding-bottom: 20px;
    }
    @include media(767px) {
      text-align: center;
      padding-bottom: 10px;
    }
    .heading {
      margin-bottom: 5px;
      @include media(767px) {
        margin-bottom: 10px;
      }
    }
    &__items {
      display: flex;
      align-items: center;
      @include media(575px) {
        flex-direction: column;
      }
    }
    &__text {
      @include media(767px) {
        font-size: 1.4rem;
        line-height: 1.8rem;
      }

    }
    &__icon {
      display: flex;
      align-items: center;
      margin-right: 5px;
    }
    &__item {
      display: flex;
      align-items: center;
      margin-right: 30px;
      &:last-child {
        margin-right: 0;
      }
      @include media(575px) {
        margin-right: 0;
        margin-bottom: 5px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &__managers {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;

    @include media(767px) {
      grid-template-columns: repeat(1, 1fr);
      grid-column-gap: 0;
      grid-row-gap: 20px;
    }

    .grid-block-leadership {
      border-top: 15px solid #6FC11C;
      padding-top: 85px;
      padding-bottom: 70px;

      @include media(767px) {
        padding-top: 45px;
        padding-bottom: 40px;
      }
      @include media(575px) {
        padding-top: 30px;
        padding-bottom: 30px;
      }

      &__subtitle {
        max-width: 160px;
        margin: 0 auto;
        @include media(767px) {
          margin-left: 0;
          max-width: 100%;
        }
      }

      &__avatar {
        width: 256px;
        height: 256px;

        @include media(991px) {
          width: 180px;
          height: 180px;
        }
        @include media(767px) {
          margin-right: 55px;
        }
        @include media(575px) {
          margin-right: 0;
          margin-left: 0;
          margin-bottom: 20px;
          width: 150px;
          height: 150px;
        }
      }
    }
  }
}