.heroimage {
  background: url("./public/images/index/heroimage.png") no-repeat top;
  height: 1080px;

  @include media(1139px) {

    background: url("./public/images/index/heroimage-md.png") no-repeat top;
    background-size: cover;
    height: 735px;
  }
  @include media(767px) {
    height: 600px;
  }
  @include media(575px) {
    height: 480px;
  }
  .content {
    padding-top: 320px;
    display: flex;
    position: relative;

    @include media(1139px) {
      padding-top: 184px;
    }
    @include media(991px) {
      padding-top: 120px;
    }
    @include media(575px) {
      padding-top: 115px;
    }
  }
  .heading {
    color: #fff;
    &_h1 {
      margin-bottom: 5px;
    }
  }
  &__description {
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 0 25px rgba(111, 136, 39, 0.07);
    backdrop-filter: blur(4px);
    border-radius: 10px;
    padding: 5px;
    position: absolute;
    left: 51%;
    bottom: -207px;

    @include media(1139px) {
      margin-top: 50px;
    }

    .decor-arrow {
      position: absolute;
      left: -75px;
      top: 75px;
    }
    &_wrapp {
      border: 1px dashed #6FC11C;
      padding: 20px 25px;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      max-width: 195px;
    }
    &_text {
      font-weight: 700;
      margin-bottom: 10px;
    }
    &_link {
      color: $linkColor;
      svg {
        margin-left: 10px;
      }
      &:hover {
        color: $linkHover;

        svg {
          path {
            fill: $linkHover;
          }

        }
      }
    }
  }
}