.main-form {
  position: relative;
  box-shadow: 0 0 75px rgba(19, 22, 52, 0.1);
  border-radius: 30%/10%;

  &__back-img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    @include media(767px) {
      display: none;
    }
  }

  &__back-img-mobile {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    @include media(767px) {
      display: block;
    }
  }

  .form {
    padding: 80px 65px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    column-gap: 60px;

    @include media(1139px) {
      padding: 60px 50px;
      column-gap: 30px;
    }

    @include media(991px) {
      padding: 60px 40px;
    }

    @include media(767px) {
      flex-direction: column;
      padding: 50px 110px;
    }

    @include media(575px) {
      padding: 40px 20px;
    }

    .heading {
      text-align: center;
      width: 100%;
      z-index: 5;
    }

    &__control-items-col1 {
      display: flex;
      flex-direction: column;
      width: 350px;

      @include media(991px) {
        width: 290px;
      }

      @include media(575px) {
        width: 250px;
      }
    }

    &__control-items-col2 {
      display: flex;
      flex-direction: column;
      width: calc(100% - 410px);

      @include media(991px) {
        width: calc(100% - 320px);
      }

      @include media(767px) {
        width: 290px;
        margin-top: 20px;
      }

      @include media(575px) {
        width: 250px;
        margin-top: 15px;
      }
    }

    &__control-items-col3 {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-top: 30px;

      @include media(767px) {
        margin-top: 20px;
      }

      @include media(575px) {
        margin-top: 15px;
      }
    }

    &__control-item {
      position: relative;
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }

      @include media(767px) {
        margin-bottom: 20px;
      }

      @include media(575px) {
        margin-bottom: 15px;
      }

      &:hover {
        .form__tooltip {
          color: $textColor2;
        }
      }

      &_textarea {
        flex-grow: 1;
      }

      &_submit {
        display: flex;
        flex-direction: column;
        align-items: center;

        .form__button {
          margin-bottom: 5px;

          @include media(991px) {
            margin-bottom: 15px;
          }

          @include media(767px) {
            margin-bottom: 5px;
          }
        }
      }

      .form__persona-info {
        color: $textColor2;
        text-align: center;

        @include media(991px) {
          font-size: 1.4rem;

          br {
            display: none;
          }
        }

        @include media(767px) {
          font-size: 1rem;
        }
      }

      .form__personal-link {
        color: $linkColor;

        &:hover {
          color: $linkHover;
        }
      }

      .form__message {
        padding: 25px 30px;
      }

    }

    &__tooltip {
      position: absolute;
      top: 25px;
      left: 30px;
      color: #B7B7B7;
    }

    &__message {
      //width: 570px;
      width: 100%;
      height: 170px;

      //@include media(1139px) {
      //  width: 450px;
      //}
      //@include media(991px) {
      //  width: 290px;
      //}
      //@include media(575px) {
      //  width: 250px;
      //}
    }
  }
}
