.btn-menu {
  display: none;

  @include media(991px) {
    display: block;
    position: relative;
    height: 3px;
    width: 26px;
    border: none;
    background: #26262c;
    border-radius: 2px;
    padding: 0;

    &:before, &:after {
      content: '';
      display: block;
      width: 26px;
      height: 3px;
      background: #26262c;
      position: absolute;
      border-radius: 2px;
    }

    &:after {
      bottom: 8px;
    }

    &:before {
      top: 8px;
    }
  }
}
